.image_container{width:100vw;height:100vh;max-width:100%;position:fixed;z-index:999;top:0}.image_container .click{position:absolute;z-index:11;width:50px;height:50px;background-color:rgba(0,0,0,0.5);text-align:center;margin:auto;cursor:pointer}.image_container .click i{margin-top:7px;color:white}.image_container .click.left{left:10px;top:0;bottom:0}.image_container .click.right{right:10px;top:0;bottom:0}.image_container .click.exit{right:0;top:0;background-color:#000}.image_container .image_background{width:100%;height:100%;background-color:rgba(0,0,0,0.9);position:absolute;z-index:9;cursor:pointer}.image_container .image_box{position:absolute;z-index:10;width:90%;height:90%;top:0;left:0;bottom:0;right:0;margin:auto}.image_container .image_box img{width:100%;height:100%;object-fit:contain}.image_container .image_box .imageL,.image_container .image_box .imageR{width:50%;height:100%;float:left;position:absolute;top:0;cursor:pointer}.image_container .image_box .imageR{right:0}.image_container .image_box .imageL{left:0}